<template>
  <div class="sites">
    <!--    主题-->
    <div class="theme">
      <div class="theme-icon">
        <slot name="theme-icon"></slot>

      </div>
      <span class="theme-name" :style="{color:color}">{{contents.theme}}</span>
    </div>
    <!--    主要网站-->
    <div class="main-site">
      <div class="main-site-item" v-for="(site, index) in contents.sites"
           @mouseenter="onMouseEnter(index, $event)" @mouseleave="onMouseLeave()">
        <a class="main-site-item-link" :href="site.main_url"
           :style="{color:color}">
          {{site.name}}
        </a>
        <!--        侧边子网站-->
        <div v-show="index===seen && site.subs.length>0">
          <div class="sub-site" :style="[displayBelow?{top:'0'}:{bottom: '-2px'}, displayLeft?{right: '300px'}:{left:'300px'}]">
            <div class="sub-site-item" v-for="sub in site.subs">
              <a class="sub-site-item-link" :href="sub.url">
                {{sub.title}}
              </a>
            </div>
          </div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
  export default {
    name: "sites",
    props: {
      color: {
        type: String,
        default: 'white'
      },
      contents: {
        type: Object,
        required: true
      },
      displayLeft: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        seen: -1,
        displayBelow: true,
      }
    },
    methods: {
      onMouseEnter(index, event) {
        this.displayBelow = event.target.getBoundingClientRect().y <= document.body.clientHeight / 2;
        // this.displayRight = event.target.getBoundingClientRect().x <= document.body.clientWidth / 2;
        this.seen = index;
      },
      onMouseLeave() {
        this.seen = -1
      },
    }
  }
</script>

<style scoped>
  .sites {
    position: relative;
    top: 10px;
    border: 2px solid white;
    border-radius: 30px;
    padding: 10px 60px 10px 60px;
  }


  .theme {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80px;
    width: 100%;

  }

  .theme-icon {
    display: inline-block;
    margin-top: 10px;
    margin-right: 15px;
  }

  .theme-name {
    font-size: 34px;
    font-weight: bold;
    text-shadow: 1px 1px 2px;
    letter-spacing: 20px;
    font-family: "联盟综艺体", sans-serif;
  }

  .main-site {
    /*position: relative;*/
    font-size: 20px;
    font-weight: bold;

  }

  .main-site-item {
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    position: relative;
    height: 42px;
    width: 300px;
    border-radius: 60px;

  }

  .main-site-item:hover {
    background-color: #4a69bd;
    border: 2px solid white;
  }

  .main-site-item-link {
    /*居中*/
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    text-decoration: none;
    height: 100%;
    width: 100%;
  }

  .sub-site {
    position: absolute;
    background-color: #4a69bd;
    border-radius: 20px;
    border: 2px solid white;
    z-index: 1;
  }

  .sub-site-item {
    height: 42px;
    width: 200px;
    border-radius: 30px;
  }

  .sub-site-item:hover {
    background-color: #b45329;
  }

  .sub-site-item-link {
    width: 100%;
    height: 100%;

    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 18px;
    font-weight: bold;
    text-decoration: none;
    color: white;

  }

</style>

